import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';

const formatDate = (dateString) => {
  const date = new Date(dateString);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0'); // 0  1 
  const day = String(date.getDate()).padStart(2, '0');
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');
  return `${year}.${month}.${day} ${hours}:${minutes}`;
};

const GuestBottom = ({ currentGuests, setGuests }) => {
  const [editingGuest, setEditingGuest] = useState(null); //   
  const [editContent, setEditContent] = useState(''); //  

  const remove = (id, event) => {
    event.preventDefault(); //    
    fetch(`http://localhost:3001/guest/${id}`, {
      method: "DELETE",
      headers: { "Content-Type": "application/json" }
    }).then((res) => {
      if (res.ok) {
        alert(".");
        //    
        setGuests(prevGuests => prevGuests.filter(guest => guest.id !== id)); //  
      } else {
        alert(" : " + res.status + " " + res.statusText);
      }
    }).catch(err => {
      console.error(" :", err);
      alert("   .");
    });
  };

  const handleEditClick = (guest) => {
    setEditingGuest(guest); //   
    setEditContent(guest.content); //   
  };

  const handleEditChange = (e) => {
    setEditContent(e.target.value); // textarea  
  };

  const handleEditSubmit = (id) => {
    fetch(`http://localhost:3001/guest/${id}`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ ...editingGuest, content: editContent }) //   
    }).then((res) => {
      if (res.ok) {
        alert(".");
        setGuests(prevGuests => 
          prevGuests.map(guest => 
            guest.id === id ? { ...guest, content: editContent } : guest
          )
        );
        setEditingGuest(null); //   
      } else {
        alert(" : " + res.status + " " + res.statusText);
      }
    }).catch(err => {
      console.error(" :", err);
      alert("   .");
    });
  };

  return (
    <div>
      {currentGuests.length === 0 ? (
        <p>  .</p>
      ) : (
        currentGuests.map((guest, index) => (
          <div className='guestbottom' key={`${guest.id}-${index}`}>
            <div className='guest-item'>
              <div className='profiletop'>
                <span>no.{guest.no}</span>
                <span>{guest.name}</span>
                <span>
                  <Link to="/">
                    <FontAwesomeIcon icon={faHome} />
                  </Link>
                </span>
                <span>({formatDate(guest.date)})</span>
                <span className='del'>
                  <button onClick={(event) => remove(guest.id, event)}></button>
                </span>
                <span className='change'>
                  <button onClick={() => handleEditClick(guest)}></button>
                </span>
              </div>
              <div className='profilebottom'>
                <div className='profile'>
                  <img src="/image/KakaoTalk_Photo_2024-09-19-18-17-35.png" alt=" " />
                </div>
                <div className='write'>
                  {editingGuest && editingGuest.id === guest.id ? (
                    <div>
                      <textarea 
                        value={editContent} 
                        onChange={handleEditChange} 
                        rows={4}
                      />
                      <button onClick={() => handleEditSubmit(guest.id)}> </button>
                    </div>
                  ) : (
                    guest.content
                  )}
                </div>
              </div>
            </div>
          </div>
        ))
      )}
    </div>
  );
};

export default GuestBottom;
