import React, { useRef, useState, useEffect } from 'react';

const GuestTop = ({ onNewGuest }) => {
  const contentRef = useRef(null);
  const [nextNo, setNextNo] = useState(0);

  const fetchGuests = async () => {
    const response = await fetch('http://localhost:3001/guest');
    const data = await response.json();
    if (data.length > 0) {
      const maxNo = Math.max(...data.map(guest => parseInt(guest.no, 10)));
      setNextNo(maxNo + 1); 
    } else {
      setNextNo(0); 
    }
  };

  useEffect(() => {
    fetchGuests(); 
  }, []);

  const write = async (e) => {
    e.preventDefault();
    const newGuest = {
      content: contentRef.current.value,
      no: nextNo, 
      id: "dptjs1106", 
      name: "",
      date: new Date().toISOString() 
    };

    const response = await fetch('http://localhost:3001/guest', {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(newGuest)
    });

    if (response.ok) {
      alert(" .");
      //      
      onNewGuest(newGuest);
      contentRef.current.value = '';
      setNextNo(nextNo + 1);
    }
  };

  return (
    <div className='guesttop'>
      <form onSubmit={write}>
        <div className='profile'>
          <img src="/image/KakaoTalk_Photo_2024-09-19-18-17-35.png" alt=" " />
        </div>
        <div className='write'>
          <textarea ref={contentRef} placeholder=" ..."></textarea>
        </div>
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default GuestTop;
